/* .App {
  text-align: center;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.List {
  list-style-type: none;
}
.Header {
  position: fixed;
  z-index: 2;
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 20px;
  background-color: white;
}
.left_side {
  display: flex;
  /* width: 30%; */
  align-items: center;
  gap: 5px;
}
ul {
  display: flex;
  gap: 20px;
  font-weight: 500;
}
#button {
  padding: 5px 15px;
  background-color: blue;
  color: white;
  border-radius: 10px;
}
.container {
  position: relative;
}

.img {
  height: 100vh;
  width: 100%;
  position: relative;
  background-image: url("https://images.unsplash.com/photo-1604014237800-1c9102c219da?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
  background-size: cover;
  background-position: center;
}
.write_side ul li:hover {
  color: blue; 
  cursor: pointer; 
}
#H1 {
  position: absolute;
  width: 30%;
  margin: 25%;
  transform: translate(-50%, -50%);
  text-align: center;
}
#Main_h1_1,
#Main_h1_2 {
  font-size: 3rem;
  font-weight: 900;
}

#Main_h1_2 {
  color: red;
}

#P_1 {
  font-size: 1.2rem;
}

#Main_Button {
  background-color: red;
  color: white;
  width: 200px;
  padding: 7px 20px;
  margin: 10px;
  border-radius: 10px;
}
#Main_Button:hover{
  background-color: white;
  color: black;
}

#Img_2 {
  margin-top: 100px;

  width: 100%;
  height: 530px;
  justify-content: space-around;
  display: flex;
}
#bg_1 {
  /* margin-left: 30px; */
  width: 50%;
}
#Text_1 {
  width: 40%;
  margin-top: 50px;
}
#Text_1_h1 {
  font-size: 28px;
  font-weight: 800;
}
#Text_1_button {
  width: 180px;
  background-color: blue;
  border-radius: 10px;
  padding: 10px;
  color: white;
  margin-top: 10px;
}
#Profile_Img {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  object-fit: cover;
}
#Information {
  padding-top: 100px;
  width: 80%;
  gap: 20px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
#Main_Information {
  width: 30%;
}
#Para_Infomation {
  background-color: ghostwhite;
  padding: 20px;
  border-radius: 30px;
}
#Infomation_Img {
  display: flex;
}
#Infomation_Name {
  margin-left: 20px;
}
.Footer {
  height: 70vh;
  padding: 30px;
  background-color: #f3f4f6;
  margin-top: 50px;

  flex-direction: column;
  text-align: center;
  /* justify-content: center; */
  display: flex;
}
.h-8 {
  color: green;
}
.List_Tag {
  justify-content: space-around;
  width: 500px;
  margin: 50px auto;
}
#Footer_Para{
  margin-top: 50px;
  width: 50%;
margin: 10px auto;
}
.logo_Footer{
  justify-content: space-evenly;
  display: flex;
  font-size: 20px;
  width: 400px;
  margin: 30px auto;

}